.calendar {
  display: flex;
  flex-wrap: wrap;
  @include calendar-size($width);
  gap: 1px;
  padding: 10px;
  align-content: center;
  @for $index from 1 through 31 {
    &--day--#{$index} {
      @include square-size($size);
      background-color: #eee;
      border: 1px solid black;
      transition: 0.5s;
    }
    &--day--#{$index}:hover {
      background-color: #ffc0cb;
      cursor: pointer;
      transform: translateY(-20px);
    }
    &--day--#{$index}::after {
      content: "#{$index}";
    }
  }

  @for $index from 28 through 31 {
    &--month-length--#{$index} >:nth-child(n+#{$index + 1}) {
      display: none;
    }
  }
  @each $day,$margin in $days {
    &--start-day--#{$day}>:first-child {
      margin-left: $margin;
    }
  }
}
